<template>
  <div class="agreement-box">
    <!-- 标题 -->
    <div class="title-box">
      <Breadcrumb :isCustom="false"></Breadcrumb>
      <div class="rightbox">
        <div class="item" @click="handlePrint()">
          <cc-svg-icon icon-class="print"></cc-svg-icon>
          <span>打印协议</span>
        </div>
      </div>
    </div>
    <!-- 协议内容 -->
    <div class="inner-box" ref="agreementInfo">
      <div class="agreement-info">
        <h3 class="agreement-title">{{ agreementInfo.agreementName || EMPTYTEXT }}</h3>
        <!-- 甲方 -->
        <div class="item-box">
          <div class="intro">
            <p>
              甲方（家庭医生团队）：<span>{{ agreementInfo.firstTeamName || EMPTYTEXT }}</span>
            </p>
            <p>
              电话：<span>{{ agreementInfo.firstTeamPhone || EMPTYTEXT }}</span>
            </p>
          </div>
          <table cellspacing="0" cellspadding="0">
            <thead>
              <tr>
                <th>团队主要人员姓名</th>
                <th>岗位</th>
                <th>职称</th>
                <th>联系电话</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, index) in agreementInfo.firstTeamJson" :key="index">
                <td>{{ item.userName || EMPTYTEXT }}</td>
                <td>{{ item.userJob || EMPTYTEXT }}</td>
                <td>{{ item.userTitle || EMPTYTEXT }}</td>
                <td>{{ item.mobile || EMPTYTEXT }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <!-- 乙方 -->
        <div class="item-box">
          <div class="intro">
            <p>乙方（居民）</p>
          </div>
          <table cellspacing="0" cellspadding="0" class="resident-box">
            <thead>
              <tr>
                <th>姓名</th>
                <th>社保（医保）卡号</th>
                <th>性别</th>
                <th>年龄</th>
                <th>家庭电话</th>
                <th>手机</th>
                <th>住址</th>
              </tr>
            </thead>
            <tbody>
              <tr v-if="agreementInfo.recordJson">
                <td>{{ agreementInfo.recordJson.userName || EMPTYTEXT }}</td>
                <td>{{ agreementInfo.recordJson.medicareCard || EMPTYTEXT }}</td>
                <td>{{ agreementInfo.recordJson.sexTypeStr || EMPTYTEXT }}</td>
                <td>{{ agreementInfo.recordJson.age || EMPTYTEXT }}</td>
                <td>{{ agreementInfo.recordJson.contactPhone || EMPTYTEXT }}</td>
                <td>{{ agreementInfo.recordJson.mobile || EMPTYTEXT }}</td>
                <td>{{ agreementInfo.recordJson.liveAddress || EMPTYTEXT }}</td>
              </tr>
            </tbody>
          </table>
        </div>
        <!-- 协议内容 -->
        <div class="agreement-content">
          {{ agreementInfo.agreementContent }}
        </div>
        <!-- 协议尾部 -->
        <div class="agreement-footer">
          <div class="leftbox">
            <P>甲方团队长签字：{{ agreementInfo.firstSiignName || EMPTYTEXT }}</P>
            <P>（单位盖章）</P>
            <P
              >日期：<span>{{
                String(agreementInfo.firstSignDateStr).slice(0, 4) || EMPTYTEXT
              }}</span
              >年<span>{{ String(agreementInfo.firstSignDateStr).slice(5, 7) || EMPTYTEXT }}</span
              >月<span>{{ String(agreementInfo.firstSignDateStr).slice(8, 10) || EMPTYTEXT }}</span
              >日</P
            >
          </div>
          <div class="rightbox">
            <P>乙方：{{ agreementInfo.secondSignName || EMPTYTEXT }}</P>
            <P
              >日期：<span>{{
                String(agreementInfo.secondSignDateStr).slice(0, 4) || EMPTYTEXT
              }}</span
              >年<span>{{ String(agreementInfo.secondSignDateStr).slice(5, 7) || EMPTYTEXT }}</span
              >月<span>{{ String(agreementInfo.secondSignDateStr).slice(8, 10) || EMPTYTEXT }}</span
              >日</P
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import printMethod from '../../../util/printMethod' //打印方法
export default {
  name: 'agreement-detail',
  data() {
    return {
      agreementInfo: {}
    }
  },
  created() {
    this.getDetailInfo() //获取协议详情
  },
  methods: {
    //获取协议详情
    getDetailInfo() {
      this.$emit('viewLoad', true)
      this.$http
        .get(process.env.VUE_APP_API + '/family/personAgreement/getDtoByAgreementId', {
          agreementId: this.$route.query.id
        })
        .then(res => {
          if (res.code === 200) {
            this.agreementInfo = res.data
            this.agreementInfo.firstTeamJson = res.data.firstTeamJson
              ? JSON.parse(res.data.firstTeamJson)
              : [] //甲方团队成员
            this.agreementInfo.recordJson = res.data.recordJson
              ? JSON.parse(res.data.recordJson)
              : {} //乙方团队成员
          } else {
            this.$message.error(res.msg)
          }
          this.$emit('viewLoad', false)
        })
        .catch(error => {
          this.$emit('viewLoad', false)
          this.$message.error(error)
        })
    },
    //打印协议
    handlePrint() {
      this.$confirm('确定要打印该协议吗？', '提示', {
        confirmButtonText: '确 认',
        cancelButtonText: '取 消',
        customClass: 'infoMessage',
        center: true
      }).then(() => {
        printMethod(this.$refs['agreementInfo'], '签约协议')
        let loading = this.$loading()
        setTimeout(() => {
          loading.close()
        }, 800)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.agreement-box {
  .inner-box {
    width: 80%;
    margin: 60px auto;
  }
}
/* 协议内容 */
.agreement-info {
  line-height: 30px;
  .agreement-title {
    font-size: 20px;
    margin-bottom: 30px;
    text-align: center;
    font-weight: bold;
  }
  .item-box {
    .intro {
      display: flex;
      align-content: space-between;
      p:first-child {
        width: 70%;
      }
      p:last-child {
        width: 30%;
      }
    }
    table {
      width: 100%;
      border-right: 1px solid #f4ebe3;
      border-top: 1px solid #f4ebe3;
      margin: 15px 0px 30px;
      table-layout: fixed;
      th,
      td {
        border-left: 1px solid #f4ebe3;
        border-bottom: 1px solid #f4ebe3;
        text-align: center;
      }
      th {
        background-color: #fcf7ed;
        padding: 0px !important;
        height: 40px;
      }
      tbody {
        td {
          line-height: 35px;
          height: 35px;
          padding: 0px !important;
        }
      }
      &.resident-box {
        th,
        td {
          &:first-child {
            width: 120px;
          }
          &:nth-child(3),
          &:nth-child(4) {
            width: 100px;
          }
        }
      }
    }
  }
  .agreement-footer {
    display: flex;
    padding-top: 30px;
    .leftbox {
      flex: 1;
    }
    .rightbox {
      width: 30%;
    }
    p {
      span {
        width: 35px;
        display: inline-block;
        text-align: center;
      }
    }
  }
}
@media print {
  .resident-box {
    table-layout: initial;
    th,
    td {
      &:first-child {
        width: 80px;
      }
      &:nth-child(5),
      &:nth-child(6) {
        width: 100px;
      }
      &:nth-child(2) {
        width: 140px;
      }
      &:nth-child(3),
      &:nth-child(4) {
        width: 60px;
      }
    }
  }
}
</style>
